import React, { useRef } from 'react';
import "./data-viewer.css"
import { Button } from '@mui/material';
import { DataGrid, GridToolbar, GridCellEditStopReasons } from '@mui/x-data-grid';
import axios from 'axios';
import EndzoneNav from '../../../components/navigators/endzone-nav/EndzoneNav'
import Footer from '../../../components/misc-items/footer/footer';
import { Alert } from '@mui/material';
import { IconButton } from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';

class ViewData extends React.Component {
    constructor() {
        super();
        this.state = {
            rows : [],
            alert: {
                style: {display: "none"},
                severity: "info",
                userMessage: ""
            },
        };

        

        this.columns = [
            { field : "Game_Year", headerName : "Game Year", valueGetter : (params) => new Date(params.row.Game_Date).getUTCFullYear(), width : 300},
            { field : "Home_Team", headerName : "Home Team", width : 300}, 
            { field : "Away_Team", headerName : "Away Team", width : 300},
            { field: 'Play_Number', headerName: 'Play Number', width : 300}, 
            { field: 'Drive', headerName: 'Drive', width : 300, editable: true},
            { field: 'Possession', headerName: 'Possession', editable: true},
            { field: 'Yard', headerName: 'Yard', width : 300, editable: true},
            { field : 'Hash', headerName : 'Hash', width : 300, editable: true},
            { field : 'Down', headerName : 'Down', editable: true},
            { field : 'Distance', headerName : 'Distance', editable: true},
            { field : 'Quarter', headerName : 'Quarter', editable: true},
            { field : 'Motion', headerName : 'Motion', editable: true},
            { field : 'D_Formation', headerName : "Defensive Formation", editable: true},
            { field : 'O_Formation', headerName : 'Offensive Formation', editable: true},
            { field : 'Formation_Strength', headerName : 'Formation Strength', editable: true},
            { field : 'Home_Score', headerName : 'Home Score', editable: true},
            { field : 'Away_Score', headerName : 'Away Score', editable: true},
            { field : 'Play_Type', headerName : 'Play Type', editable: true},
            { field : 'Pass_Zone', headerName : 'Pass Zone', editable: true},
            { field : 'Coverage', headerName : 'Coverage', editable: true},
            { field : 'Play_Type_Dir', headerName : 'Play Direction', editable: true},
            { field : "Play", headerName : "Play", editable: true},
            { field : 'Pressure_Left', headerName : 'Pressure Left', editable: true},
            { field : 'Pressure_Middle', headerName : 'Pressure Middle', editable: true},
            { field : 'Pressure_Right', headerName : 'Pressure Right', editable: true},
            { field : 'Ball_Carrier', headerName : 'Ball Carrier', editable: true},
            { field : 'Event', headerName : 'Event', editable: true},
            { field : 'Creation_Date', headerName : 'Creation Date'},
            { field : 'Delete Game', renderCell:(cellValues)=>{
                return(
                    <Button variant = "contained" color ="primary" onClick = {(e)=>{
                        let id = cellValues.row.id
                        this.handleClick(id)
                    }}>Delete</Button>
                )
            }}
          ];
    }
    
    handleClick = (id) => {
        const confirmBox = window.confirm("Are you sure you want to delete this play?");
        if (confirmBox === true){
            axios.post(`/endzone/utils/play/delete`, {
                id : id
            }).then((response) => {
                let rows = this.state.rows;
                rows = rows.filter(row => row.id !== id);


                this.setState({
                    alert: {
                        style: {display: "block"},
                        severity: "success",
                        userMessage: `Successfully Deleted ${id}`
                    },
                    rows : rows   
            })},(reject) => {
                this.handleError(reject)
            })
        }
      };
    

    getUrlParams = () => {
        const queryString = window.location.search
        const urlParams = new URLSearchParams(queryString)
        let gameId = urlParams.get("gameId")
        return gameId
    }

    componentDidMount = () => {
        this.getPlays()
    }


    getPlays = () => {
        let url = '/endzone/utils/play/get'
        console.log(this.state)
        let gameId = this.getUrlParams()
        console.log(gameId)
        if (gameId){
            url = url + '?gameId=' + gameId
            console.log(url)
        }
        
        
        axios.get(url).then((response) => {
            console.log(response.data)
            this.setState({rows:response.data})
        },(reject) => {
            this.handleError(reject)
        })
    }

    handleError = (response) => {
        try {
            console.log(response)
            this.setState({alert: {
                style: {display: "block"},
                severity: "error",
                userMessage: response.response.data
            }})
        }
        catch {
            this.setState({alert: {
                style: {display: "block"},
                severity: "error",
                userMessage: "Unexpected Error - Please Contact: endzone.support@gmail.com"
            }})
        }
    }

    closeAlert = () => {
        let alert =  {
            style: {display: "none"},
            severity: "info",
            userMessage: ""
        }
        this.setState(
            {alert : alert}
        )
    }
    
    handleCellEditStop = (params) => { // chat gpt said field is the cell being changed, and value is what the new value is
        const { id, field, value } = params;
        console.log(params)
        // Make an Axios request to your API endpoint
        axios.post(`/endzone/utils/play/update`, { // I think there is something wrong with this post request
          id: id,
          column: field,
          value: value,
        }).then((response) => {
            this.setState({
                alert: {
                    style: {display: "block"},
                    severity: "success",
                    userMessage: `Successfully Updated ${id}`
                }
            })
            
            // Handle the successful response, if necessary
            console.log('Data updated successfully:', response.data);
          },(error) => {
            // Handle the error, if necessary
            console.error('Error updating data:', error);
          });
      };

    render(){
        return(
            <div className = 'manage-page'>
                <div className = 'top-nav-container'><EndzoneNav links={[{title: "Return to the Data Hub", link: "/endzone/data/hub"},]}></EndzoneNav></div>
                <div className = 'data-viewer'> 
                <div className='alert-div' style = {this.state.alert.style}>
                        <Alert variant="filled" severity={this.state.alert.severity}
                            action={
                                <IconButton
                                    aria-label="close"
                                    color="inherit"
                                    size="large"
                                    onClick={() => {
                                        this.closeAlert();
                                    }}
                                >
                                <CloseIcon fontSize="inherit" />
                                </IconButton>
                            }>
                            {this.state.alert.userMessage}
                        </Alert>
                    </div>
                    <div className = 'data-grid-contain'>
                        <DataGrid className = 'data-grid' style = {{"color" : "black","font-size":"1.85vh","backgroundColor" : 'white', "height":"100%", "width" : "100%", "border": "3px solid #0984e3", "fontFamily":"Audiowide"}} rows={this.state.rows} columns={this.columns}
                        onCellEditCommit = {(params)=>{
                            this.handleCellEditStop(params)
                        }}></DataGrid>
                    </div>
                </div>
                <div className = "bottom-bar" > 
                    <div className = "footer"><Footer></Footer></div>
                </div>
            </div>


        );
    }

}
        
export default ViewData;        